import React from "react";

class Manager extends React.Component {
  componentDidMount() {
    this.callurl();
  }
  render() {
    return <div> !!~ </div>;
  }
  callurl = () => {
    const ClickSave = {
      method: "GET",
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      cors: "no-cors",
    };
    fetch("/abouts", ClickSave).then((res) => console.log(res.json()));
  };
}
export default Manager;
