import React, { useState } from "react";
import "../css/Paging.css";
import Pagination from "react-js-pagination";

const Paging = ({ maxitems }) => {
  const [page, setPage] = useState(1); //  
  const [totalCount, setTotalCount] = useState(20); //  Row

  //   const resultPageNum = maxitems / 20; //  1  (Row 20 )   
  //   const restPageNum = maxitems % 20; // 20   0    

  //   if (resultPageNum > 1) {
  //     setpageRange(resultPageNum);
  //   }
  //   if (maxitems > 20 && restPageNum > 0) {
  //     setTotalCount(restPageNum);
  //   }

  //  
  const handlePageChange = (page) => {
    setPage(page);
  };

  return (
    <Pagination
      activePage={page}
      itemsCountPerPage={totalCount}
      totalItemsCount={maxitems}
      prevPageText={""}
      nextPageText={""}
      onChange={handlePageChange}
    />
  );
};

export default Paging;
