import React, { useState } from "react";
import { FileUploader } from "react-drag-drop-files";
import "../css/modal-style.css";

class Modal extends React.Component {
  render() {
    return (
      <div id="modal">
        <div className="modal-content">
          <span className="close" onClick={this.CloseModal}>
            &times;
          </span>
          <DragDrop></DragDrop>
          <div className="uploadSection">
            <button className="uploadbtn" onClick={this.RequestUrl}>
               
            </button>
          </div>
        </div>
      </div>
    );
  }
  CloseModal = () => {
    const modal = document.getElementById("modal");
    modal.style.display = "none";
  };
  RequestUrl = () => {
    let fileTag = document.getElementsByName("file");
    let fileData = fileTag[0].files[0];
    this.props.tabledata(fileData);
    let formData = new FormData();
    formData.set("file", fileData);
    console.log(fileTag);
    // const RequestData = {
    //   method: "POST",
    //   headers: {
    //     "Content-Type": "multipart/form-data",
    //   },
    //   body: formData,
    // };
    // fetch("", RequestData).then((showList) => showList.text());
    this.CloseModal();
  };
}

//  
const fileTypes = ["WAV", "PCM", "RAW"];

function DragDrop() {
  const [file, setFile] = useState(null);
  const handleChange = (file) => {
    setFile(file);
  };
  const completeMessage = () => {
    document.querySelector(".dropzone").firstChild.innerText = " ";
  };
  return (
    <div className="AttachmentArea">
      <h1>WAV  </h1>
      <FileUploader
        children={
          <div className="dropzone">
            <span>    .</span>
          </div>
        }
        multiple={true}
        handleChange={handleChange}
        name="file"
        types={fileTypes}
        hoverTitle=" "
        onTypeError={() => {
          alert("   .\n WAV  .");
        }}
        onDrop={completeMessage}
        onSelect={completeMessage}
      />
      <p>
        {file
          ? file[0]
            ? `: ${file[0].name}`
            : "  ."
          : "   ."}
      </p>
    </div>
  );
}

export default Modal;
